export default IndexPage

export const query = graphql`
{
  allPrismicPage {
    edges {
      node {
        id
        uid
        data {
          title {
            text
          }
          text {
            text
          }
          color
          image_gallery {
            image {
              alt
              url
              localFile {
                childImageSharp {
                  fluid(maxWidth: 1500) {
                    ...GatsbyImageSharpFluid_withWebp
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}
`
